import axios from 'axios'


//
axios.defaults.baseURL = 'http://api.duyiedu.com';
axios.interceptors.request.use(function (config) {
    console.log (config);
    config.params = {
        ...config.params, 
        appkey: "znlxuexi_1553155994401"
    }
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

